<template>
  <div class="single-article">
    <h2>{{ article.title }}</h2>
    <div class="article-info">
      <ul class="tags left">
        <li v-for="tag in article.tags" v-bind:key="tag.id" class="tag">{{ tag }}</li>
      </ul>
      <ul class="center"></ul>
      <ul class="right">
        <li class="date">{{ article.date | toDate }}</li>
      </ul>
    </div>
    <div v-html="mdRender" class="content md-content"></div>
    <footer>
      <ul class="left">
        <li>
          <a @click="back2Reader" class="back">Back</a>
        </li>
      </ul>
      <ul class="center">
        <li></li>
      </ul>
      <ul class="right">
        <li></li>
      </ul>
    </footer>
  </div>
</template>

<script>
///// Parsers //////
import prism from "./prism";
import "../assets/prism.css";
// katex
import katex from "katex";
import "katex/dist/katex.min.css";
import renderMathInElement from "katex/dist/contrib/auto-render.min";
// Markdown parser
let marked = require("marked");

// Event bus
import { bus } from '../main';

export default {
  props: {
    article: Object,
    viewArticle: Object
  },

  /*
  watch: {
    'viewArticle.idx': function() {
      setTimeout(() => {this.renderMath();}, 4000);
    }
  },
  */

  data() {
    return {
      id: parseInt(this.$route.params.id),
      idIsValid: true
      //articles: [],
    };
  },

  methods: {
    back2Reader: function() {
      bus.$emit('toReader', true);
    }
  },

  filters: {
    toDate: function(value) {
      var date = new Date(value);
      return date.toISOString().substr(0, 10);
    }
  },

  computed: {
    mdRender: function() {
      // render Markdown
      var temp = document.createElement("div");
      temp.innerHTML = marked(this.article.content);
      return temp.innerHTML;
    }
  },

  created() {},

  mounted() {
    var target = document.querySelector(".md-content");
    if (target) {
      renderMathInElement(target, {
        delimiters: [
          { left: "$$", right: "$$", display: true },
          { left: "\\[", right: "\\]", display: true },
          { left: "$", right: "$", display: false },
          { left: "\\(", right: "\\)", display: false }
        ]
      });
    }
  }
};
</script>

